import { getObjectKey } from './getObjectKey';

/*TODOS: 
  - –¥–æ–ø–∏—Å–∞—Ç—å –æ–±—Ä–∞–±–æ—Ç–∫—É –º–∞—Å—Å–∏–≤–æ–≤ mas[0]
  - –±–∞–≥. –µ—Å–ª–∏ –Ω–∞–∑–≤–∞–Ω–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –æ–¥–∏–Ω–∞–∫–æ–≤—ã–µ, —Ç–æ –æ–Ω–∏ –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª—è—é—Ç—Å—è.
*/

const Templator = (function () {
  class Templator {
    TEMPLATE_REGEXP = /\{\{(.*?)\}\}/i;

    constructor(template) {
      this._template = template;
    }

    compile(ctx) {
      return this._compileTemplate(this._template, ctx);
    }

    _compileTemplate(template, ctx) {
      let tmpl = this._template;
      let key = null;
      const regExp = this.TEMPLATE_REGEXP;

      while ((key = regExp.exec(tmpl))) {
        if (key[1]) {
          const tmplValue = key[1].trim();
          const data = getObjectKey(ctx, tmplValue);

          if (typeof data === 'function') {
            /*
              –±–∞–≥. –µ—Å–ª–∏ –Ω–∞–∑–≤–∞–Ω–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –æ–¥–∏–Ω–∞–∫–æ–≤—ã–µ, —Ç–æ –æ–Ω–∏ –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª—è—é—Ç—Å—è.. 
              –Ω—É–∂–Ω–æ –ø–æ–¥—É–º–∞—Ç—å –∫–∞–∫ —Å —ç—Ç–∏–º –±—ã—Ç—å, –Ω–æ –ø–æ–∫–∞ –ø—Ä–æ—Å—Ç–æ –¥–æ–±–∞–≤–ª—è—é —Ö–µ—à
            */
            const newFuncName = `${tmplValue}_${Math.floor(
              Math.random() * 500
            )}`;
            window[newFuncName] = data;

            tmpl = tmpl.replace(
              new RegExp(key[0], 'gi'),
              `window.${newFuncName}()`
            );
            continue;
          }

          tmpl = tmpl.replace(new RegExp(key[0], 'gi'), data);
        }
      }

      return tmpl;
    }
  }
  return Templator;
})();

export function getTemplate(template, context) {
  const tmpl = new Templator(template);

  return tmpl.compile(context); // –°—Ç—Ä–æ–∫–∞ —Å html-–≤—ë—Ä—Å—Ç–∫–æ–π
}
