import DialogsItem from '../../components/DialogsItem/DialogsItem';
import Message from '../../components/Message/Message';
import SendMessange from '../../components/SendMessange/SendMessange';
import UserSetting from './modules/UserSetting/UserSetting';
import { dialogs } from './data';

const chat = () => {
  const MainContainer = document.querySelector('.chat__main');
  const chatContainer = document.querySelector('.chat__container');
  const userForm = [
    {
      name: 'email',
      type: 'text',
      placeholder: '–≤–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç—É',
      title: '–ü–æ—á—Ç–∞',
      value: 'pochta@yandex.ru',
    },
    {
      name: 'login',
      type: 'text',
      placeholder: '–≤–≤–µ–¥–∏—Ç–µ –õ–æ–≥–∏–Ω',
      title: '–õ–æ–≥–∏–Ω',
      value: 'ivanivanov',
    },
    {
      name: 'first-name',
      type: 'text',
      placeholder: '–≤–≤–µ–¥–∏—Ç–µ –∏–º—è',
      title: '–ò–º—è',
      value: '–ò–≤–∞–Ω',
    },
    {
      name: 'second-name',
      type: 'text',
      placeholder: '–≤–≤–µ–¥–∏—Ç–µ —Ñ–∞–º–∏–ª–∏—é',
      title: '–§–∞–º–∏–ª–∏—è',
      value: '–ò–≤–∞–Ω–æ–≤',
    },
    {
      name: 'phone',
      type: 'text',
      placeholder: '–≤–≤–µ–¥–∏—Ç–µ —Ç–µ–ª–µ—Ñ–æ–Ω',
      title: '–¢–µ–ª–µ—Ñ–æ–Ω',
      value: '+7 (909) 967 30 30',
    },
    {
      name: 'password',
      type: 'password',
      placeholder: '–≤–≤–µ–¥–∏—Ç–µ –ü–∞—Ä–æ–ª—å',
      title: '–ü–∞—Ä–æ–ª—å',
      value: 'qwerty',
    },
  ];

  const openDialog = (index) => {
    chatContainer.innerHTML = '';
    chatContainer.classList.add('chat__container--dialog');

    dialogs?.[index].messages.forEach((item, index) => {
      let div = document.createElement('div');
      div.className = 'chat__messange';
      div.innerHTML = Message({
        ...item,
      });

      chatContainer.prepend(div);
    });

    let div = document.createElement('div');
    div.className = 'chat__messange';
    div.innerHTML = SendMessange();
    chatContainer.append(div);
  };

  const getUserSettings = () => {
    // –í–´–í–û–î –ù–ê–°–¢–†–û–ï–ö –ü–û–õ–¨–ó–û–í–ê–¢–ï–õ–Ø

    MainContainer.innerHTML = '';

    let div = document.createElement('div');
    div.className = 'chat__user-setting';
    div.innerHTML = UserSetting({
      userForm,
    });
    MainContainer.append(div);
  };

  const getContacts = () => {
    // –í–´–í–û–î –ö–û–ù–¢–ê–ö–¢–û–í
    MainContainer.innerHTML = '—Ä–∞–∑—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç—Å—è..';
  };
  const getSettings = () => {
    // –í–´–í–û–î –ù–ê–°–¢–†–û–ï–ö –ü–†–ò–õ–û–ñ–ï–ù–ò–Ø
    MainContainer.innerHTML = '—Ä–∞–∑—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç—Å—è..';
  };

  const getDialogs = () => {
    // –í–´–í–û–î –°–ü–ò–°–ö–ê –î–ò–ê–õ–û–ì–û–õ–û–í
    MainContainer.innerHTML = '';

    dialogs?.forEach((item, index) => {
      let div = document.createElement('div');
      div.innerHTML = DialogsItem({
        ...item,
        lastMessange: item.messages?.[0],
        openDialog: () => {
          openDialog(index);
        },
      });

      MainContainer.append(div);
    });
  };

  const menuFunction = [getUserSettings, getDialogs, getContacts, getSettings];
  const chatMenu = document.querySelector('.chat__menu');
  const chatMenuButton = document.querySelectorAll('.chat__icon');
  chatMenuButton.forEach((item, index) => {
    item.addEventListener('click', () => {
      menuFunction[index]();
      chatMenu.className = `chat__menu chat__menu--active-${index}`;
    });
  });

  // –ø—Ä–∏ –ø–µ—Ä–≤–æ–º –æ—Ç–∫—Ä—ã—Ç–∏–∏ –ø–æ–∫–∞–∑—ã–≤–∞—é —Å–ø–∏—Å–æ–∫ —á–∞—Ç–æ–≤
  menuFunction[1]();
  // –ø—Ä–∏ –ø–µ—Ä–≤–æ–º –æ—Ç–∫—Ä—ã—Ç–∏–∏
  chatContainer.innerHTML = `<p class="chat__subtitle">–í—ã–±–µ—Ä–∏—Ç–µ —á–∞—Ç —á—Ç–æ–±—ã –æ—Ç–ø—Ä–∞–≤–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ</p>`;
};

export default chat();
